<template>
  <v-container>
    <v-layout
      column
    >
      <v-card>
        <v-container
          fluid
          grid-list-lg
          row
        >
          <v-layout row wrap>
            <v-flex xs12>
              <v-card color="blue-grey darken-2" class="white--text">
                <v-card-title primary-title>
                  <div class="headline">風扇控制: </div> 
                </v-card-title>
                <v-card-actions>
                  <v-flex>
                    <v-btn color="success" @click="fanControll('on')">Turn on</v-btn>
                    <v-btn color="error" @click="fanControll('off')">Turn off</v-btn>
                    <v-btn color="primary" @click="fanControll('auto')">Auto</v-btn>
                  </v-flex>
                </v-card-actions>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
      <v-card>
        <v-container
          fluid
          grid-list-lg
          row
        >
          <v-layout row wrap>
            <v-flex xs12>
              <v-card color="blue-grey darken-2" class="white--text">
                <v-card-title primary-title>
                  <div class="headline">電燈開關: </div> 
                </v-card-title>
                <v-card-actions>
                  <v-flex>
                    <v-btn color="success" @click="lightControll(900)">Turn on</v-btn>
                    <v-btn color="error" @click="lightControll(0)">Turn off</v-btn>
                  </v-flex>
                </v-card-actions>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  methods: {
    fanControll: function(val) {
      if (val === "on") {
        this.$mqtt.publish(
          "KevinFan/lab305/auto",
          JSON.stringify({ auto: 0, fan: val })
        );
      } else if (val === "off") {
        this.$mqtt.publish(
          "KevinFan/lab305/auto",
          JSON.stringify({ auto: 0, fan: val })
        );
      } else if (val === "auto") {
        this.$mqtt.publish("KevinFan/lab305/auto", JSON.stringify({ auto: 1 }));
      } else {
        console.log("no mode!");
      }
    },
    lightControll(val) {
      this.$mqtt.publish("/iot/2f/sensor/adc", JSON.stringify({ adc: val }));
    }
  }
};
</script>

